



























































































import Vue from 'vue'
import Bounce from 'bounce.js'
import { Route } from 'vue-router'

export default Vue.extend({
  data() {
    return {
      selectionIndicator: {} as Element,
      menuItems: [] as HTMLLinkElement[],
    }
  },
  watch: {
    $route(to: Route, from: Route) {
      const menuItem = this.findMenuItem(to.path)
      if (menuItem !== undefined)
        this.animateSelector(menuItem)
    },
  },
  methods: {
    findMenuItem(path: string): HTMLLinkElement|undefined {
      return this.menuItems.find(item => item.href.endsWith(path))
    },
    animateSelector(menuItem: HTMLLinkElement) {
      const targetRect = menuItem.getBoundingClientRect()
      const indicatorRect = this.selectionIndicator.getBoundingClientRect()

      const bounce = new Bounce()
      bounce.translate({
        from: { x: indicatorRect.left, y: indicatorRect.top },
        to: { x: targetRect.left, y: targetRect.top },
        bounces: 0,
      })
      bounce.applyTo(this.selectionIndicator)
    },
    moveSelector(target: HTMLLinkElement) {
      const targetRect = target.getBoundingClientRect()
      const bounce = new Bounce()
      bounce.translate({
        from: { x: targetRect.left, y: targetRect.top },
        to: { x: targetRect.left, y: targetRect.top },
        bounces: 0,
      })
      bounce.applyTo(this.selectionIndicator)
    },
    setupMenuItems() {
      this.menuItems = Array.from(this.$el.querySelectorAll('ul#menu > a'))
    },
    setupSelectionIndicator() {
      const indicator = this.$el.querySelector('#selectionIndicator') as HTMLDivElement
      if (indicator !== null)
        this.selectionIndicator = indicator
      const selectedMenuItem = this.findMenuItem(this.$route.path)
      if (selectedMenuItem !== undefined)
        this.moveSelector(selectedMenuItem)
    },
  },
  mounted() {
    this.setupMenuItems()
    this.setupSelectionIndicator()
  },
})
